import React from "react";
import "./pagination.style.css";
import _ from "lodash";
import PropTypes from "prop-types";

const Pagination = (props) => {
  const { itemsCount, itemsPerPage, currentPage, onPageChange } = props;
  const pagesCount = Math.ceil(itemsCount / itemsPerPage);
  if (pagesCount === 1 && currentPage === 1) return null;
  const pages = _.range(1, pagesCount + 1);
  return (
    <nav>
      <ul className="pagination">
        <li
          className={
            currentPage === 1 ? "pagination__item disabled" : "pagination__item"
          }
          onClick={() => onPageChange(currentPage - 1, pagesCount)}
        >
          <a className="pagination__link">Prev</a>
        </li>
        {pages.map((page) => (
          <li
            key={page}
            className={
              page === currentPage
                ? "pagination__item active"
                : "pagination__item"
            }
            onClick={() => onPageChange(page, pagesCount)}
          >
            <a className="pagination__link">{page}</a>
          </li>
        ))}
        <li
          className={
            currentPage === pagesCount
              ? "pagination__item disabled"
              : "pagination__item"
          }
          onClick={() => onPageChange(currentPage + 1, pagesCount)}
        >
          <a className="pagination__link">Next</a>
        </li>
      </ul>
    </nav>
  );
};

Pagination.propTypes = {
  itemsCount: PropTypes.number.isRequired,
  itemsPerPage: PropTypes.number.isRequired,
  onPageChange: PropTypes.func.isRequired,
};

export default Pagination;
