import React, { Component } from "react";
import "./App.css";
import Overview from "./components/overview/overview.component";
import Form from "./components/form/form.component";
import List from "./components/list/list.component";
import Pagination from "./components/common/pagination/pagination.component";
import { paginate } from "./util/paginate";

class App extends Component {
  state = {
    items: [
      { desc: "test1", id: 1, type: "inc", value: 10 },
      { desc: "test2", id: 2, type: "inc", value: 20 },
      { desc: "test3", id: 3, type: "inc", value: 30 },
      { desc: "test4", id: 4, type: "inc", value: 40 },
      { desc: "test5", id: 5, type: "inc", value: 50 },
      { desc: "test6", id: 6, type: "inc", value: 60 },
      { desc: "test7", id: 7, type: "inc", value: 70 },
      { desc: "test8", id: 8, type: "inc", value: 80 },
      { desc: "test9", id: 9, type: "inc", value: 90 },
    ],
    itemsPerPage: 4,
    currentPage: 1,
  };

  handlePageChange = (page, maxPageCount) => {
    if (0 < page && page <= maxPageCount) this.setState({ currentPage: page });
  };

  handleDelete = (item) => {
    const items = this.state.items.filter((i) => i.id !== item.id);
    this.setState({ items });
  };

  handleAddItem = (item) => {
    const newItem = { id: Math.random(), ...item };
    const items = [...this.state.items, newItem];
    this.setState({ items });
  };

  render() {
    const { length: count } = this.state.items;
    const { items: allItems, currentPage, itemsPerPage } = this.state;
    const items = paginate(allItems, currentPage, itemsPerPage);

    return (
      <React.Fragment>
        <Overview
          inc={allItems.filter((item) => item.type === "inc")}
          exp={allItems.filter((item) => item.type === "exp")}
          formatNum={this.formatNumber}
        />
        <Form onAddItem={this.handleAddItem} />
        <div className="container">
          <List
            items={items}
            onDelete={this.handleDelete}
            formatNum={this.formatNumber}
          />
          <Pagination
            itemsCount={count}
            itemsPerPage={itemsPerPage}
            currentPage={currentPage}
            onPageChange={this.handlePageChange}
          />
        </div>
      </React.Fragment>
    );
  }

  formatNumber(type, value) {
    value = parseInt(value, 10).toLocaleString("en", {
      style: "decimal",
      minimumFractionDigits: 2,
    });

    return type ? (type === "inc" ? `+ ${value}` : `- ${value}`) : value;
  }
}

export default App;
